"use client";

import { useEffect, useRef } from "react";
import mapboxgl from "mapbox-gl";

export default function MapPage() {
  const mapContainer = useRef(null);

  useEffect(() => {
    mapboxgl.accessToken = process.env.NEXT_PUBLIC_MAPBOX_TOKEN;

    const map = new mapboxgl.Map({
      container: mapContainer.current,
      style: "mapbox://styles/mapbox/streets-v12",
      center: [-111.93, 33.42],
      zoom: 12,
    });

    map.addControl(new mapboxgl.NavigationControl(), "top-right");
    map.addControl(new mapboxgl.FullscreenControl(), "top-right");

    return () => map.remove();
  }, []);

  return (
    <div className="relative w-screen h-screen">
      {/* Map container */}
      <div ref={mapContainer} className="absolute inset-0" />

      {/* Top title bar */}
      <div className="absolute top-0 left-0 w-full bg-black bg-opacity-60 text-white text-center py-2 z-10">
        Zonix | Tempe Zoning Map
      </div>

      {/* Bottom info panel */}
      <div className="absolute bottom-0 left-0 w-full bg-black bg-opacity-60 text-white text-center py-3 z-10">
        <p>Zoning Types: Residential | Commercial | Industrial</p>
        <p>Click on parcels for details (coming soon!)</p>
      </div>
    </div>
  );
}

